import React, { Suspense, useState } from "react";
import {
  HashRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
 
import Mainheader from "./components/mainheader";
import Home from "./home";
import Sidebar from "./components/sidebar";
import Appuserscreen from "./users/screen/Appuserscreen";
import {  useSelector } from "react-redux";
const Leitnerhome = React.lazy(() => import("./leirtner/home"));

function App() {
  const userlogin = useSelector((state) => state.userlogin);
  const [sidestate, sidegir] = useState(false);
  const [kolstate, kolgir] = useState(false);

  const sidebarclickhandler = () => {
    sidegir((p) => !p);
    setTimeout(() => {
      kolgir((p) => !p);
    }, 100);
  };
  const sidbaron = () => {
    setTimeout(() => {
      sidegir((p) => !p);
    }, 400);
    setTimeout(() => {
      kolgir((p) => !p);
    }, 300);
    
  };

  return (
  
    <Router> 
      <Mainheader onClick={sidbaron} />
      {kolstate && (
        <Sidebar
          sidestate={sidestate}
          kolstate={kolstate}
          onClick={sidebarclickhandler}
        />
      )}
      <Suspense fallback={<div>Loading...</div>}>
        <Switch>
          <Route component={Home} exact path="/" />
          <Route path="/leitner">
            {userlogin.userinfo ? (<Leitnerhome /> ) : ( <Redirect to="/users/login" /> )}
          </Route>
          <Route component={Appuserscreen} path="/users" />
        </Switch>
      </Suspense>
    </Router>
  );
}

export default App;
