import React from "react";
import { Link } from "react-router-dom";
import {  useSelector } from "react-redux";
import style from "./sidebar.module.css";

const Sidebar = (props) => {
  const data = useSelector((state) => state.headerdata);
 
  const classname = data&&data.headerdata[1]&&data.headerdata[1].className;
  let hclass;
  switch (classname) {
    case "users":
      hclass = style.users;
      break;
    case "leitner":
      hclass = style.leitner;

      break;

    default:
      break;
  }
  return (
    <>
      {props.kolstate && (
        <div className={style.kol} onClick={props.onClick}>
          <div
            className={`${style.sidebar} ${hclass} ${
              props.sidestate ? style.sidebarshow : style.sidebarclosed
            }`}
          >
            <nav>
              <ul>
                <li>
                  <Link to="/users/profile/">USER</Link>
                </li>
                <li>
                  <Link to="/leitner/">LEITNER</Link>
                </li>
                <li>
                  <Link to="/leitner/page2">yechi2</Link>
                </li>
                <li>
                  <Link to="/leitner/page3">Level 3</Link>
                </li>
                <li>
                  <Link to="/leitner/page4">Level 4</Link>
                </li>
                <li>
                  <Link to="/leitner/page5">Level 5</Link>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      )}
    </>
  );
};

export default Sidebar;
